import react, { useState } from "react";
import { Card, CardContent, TextField, Button, CircularProgress, Grid } from "@material-ui/core";


const GamePage = ({ socket, setPage }) => {
    let timer = null;
    let data = null;
    const [waiting, setWaiting] = true;
    const [seconds, setSeconds] = useState(0);
    const time = 20;
    socket.on("question", (req) => {
        data = req;
        setWaiting(false);
        timer = setInterval(() => {
            setSeconds(seconds + 1);
            if (seconds == time) {
                socket.emit("timeup");
            }
        }, 1000)
    });


    const answer = (index) => {
        socket.emit("answer", index);
        clearInterval(timer);
        setWaiting(true);
    }

    return (
        <>
            <Card>
                <CardContent>
                    {
                        !waiting ?
                            <>
                                <h1>{data.question}</h1>
                                <Grid>
                                    <Grid item xs={6}>
                                        <Button size="large" variant="outlined" onClick={answer(0)}>{data.answer[0]}</Button>
                                    </Grid>
                                    <Grid item xs={6}>
                                        <Button size="large" variant="outlined" onClick={answer(1)}>{data.answer[1]}</Button>
                                    </Grid>
                                    <Grid item xs={6}>
                                        <Button size="large" variant="outlined" onClick={answer(2)}>{data.answer[2]}</Button>
                                    </Grid>
                                    <Grid item xs={6}>
                                        <Button size="large" variant="outlined" onClick={answer(3)}>{data.answer[3]}</Button>
                                    </Grid>
                                </Grid>
                                <p>Remaining time: {time - seconds} seconds</p>
                            </>
                            :
                            <>
                                <CircularProgress />
                                <p>Waiting for question...</p>
                            </>
}
                </CardContent>
            </Card>
        </>);
}

export default GamePage;
