import './App.css';
import HomePage from './components/pages/HomePage';
import { Grid } from "@material-ui/core";
import io from "socket.io-client";
import { useState } from 'react';
import ErrorPage from './components/pages/ErrorPage';
import RefusedPage from './components/pages/RefusedPage';
import GamePage from "./components/pages/GamePage";
import WonPage from './components/pages/WonPage';
import LostPage from './components/pages/LostPage';

function App() {
    const socket = io();
    const [page, setPage] = useState("home");
    const [admin, setAdmin] = useState(false);
    const [place, setPlace] = useState(0);
    socket.on("refused", () => {
        setPage("refused");
    });
    socket.on("admin", () => {
        setAdmin(true);
    })
    socket.on("start", () => {
        page = "game";
    })
    socket.on("wrong", (req) => {
        setPlace(req);
        setPage("lost");
    })
    socket.on("won", () => {
        setPage("won");
    })

    const startCallback = () => {
        socket.emit("start");
    }


    return (
        <Grid container
            spacing={0}
            direction="column"
            alignItems="center"
            justify="center" style={{ textAlign: "center", minHeight: '100vh' }}>
            <Grid>
                {
                    page == "home" ? <HomePage admin={admin} startCallback={startCallback} /> : page == "refused" ? <RefusedPage /> : page == "game" ? <GamePage  /> : page == "won" ? <WonPage /> : page == "lost" ? <LostPage place={place} /> : <ErrorPage />
                }
                <div class="copy">
                    <p>Quiz Battle Royal &copy; 2021 Ruben Kober</p>
                </div>
            </Grid>
        </Grid>
    );
}

export default App;
